.emas__home-header {
    background-image: url("../../../assets/images/homepage/transmission1.jpeg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    z-index: 20;
}

.emas__home-header_container {
    color: white;
    width: 40vw;
    padding: 20vw 0;
    padding-top: 20vw;
    padding-left: 20vw;
}

.emas__home-header_container h1 {
    font-size: 60px;
    margin-bottom: 0;
    line-height: 120%;
}

.emas__home-header_container p {
    font-size: 32px;
    line-height: 120%;
    font-weight: 500;
}
@media screen and (max-width: 1000px) {
    .emas__home-header_container {
        color: white;
        width: 60vw;
        padding: 20vw 0;
        padding-top: 20vw;
        padding-left: 20vw;
    }
}
@media screen and (max-width: 414px) {
    .emas__home-header_container {
        width: auto;
        padding: 6rem 2rem;
        padding-top: 8rem;
    }

    .emas__home-header_container h1 {
        font-size: 35px;
    }
    .emas__home-header_container p {
        font-size: 20px;
        line-height: 100%;
        font-weight: normal;
    }
}

