.emas__elecuses {
    display: flex;
    flex-direction: column;
    background-color: white;
    padding: 10vw 10vw;
}

.emas__elecuses h1 {
    text-align: center;
    font-size: 3vw;
}

.emas__elecuses-content {
    display: flex;
    flex-direction: column;
}

.emas__elecuses-content h1 {
    text-align: left;
    font-size: 2.5vw;
}

.emas__elecuses-content p {
    font-size: 1.1vw;
}

.emas__elecuses-content_div {
    gap: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 4rem;
}

.emas__elecuses-content_div div {
    width: 50%;
}

.emas__elecuses-content_div img {
    width: 40vw;
}

.emas__elecuses-content_div-image {
    display: none;
}

@media screen and (max-width: 414px){
    .emas__elecuses {
        padding: 3rem 2rem;
    }
    .emas__elecuses-content_div {
        flex-direction: column;
        padding-bottom: 1rem;
    }
    .emas__elecuses-content_div div {
        width: 100%;
    }
    .emas__elecuses h1 {
        font-size: 30px;
    }
    .emas__elecuses-content h1 {
        font-size: 25px;
    }
    
    .emas__elecuses-content p {
        font-size: 14px;
    }
    .emas__elecuses-content_div-image {
        display: flex;
    }
    .emas__elecuses-content_div img {
        width: 100%;
    }
    .emas__elecuses-content_image2 {
        display: none;
    }
    
    
}