
.emas__published {
    padding-bottom: 2rem;

}
.emas__published p {
    font-size: 16px;
}

.emas__published-content {
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid black;
    align-items: center;
    
}

.emas__published-content_top {
    display: flex;
    flex-direction: column;
    padding-left: 1rem;
    
}
.emas__published-content_top h1 {
    margin-bottom: -1rem;
    margin-top: 1.5rem;
    font-size: 20px;
}
.emas__published-content_top p {
    font-size: 15px;
}

.emas__published img {
    border-radius: 40px;
    width: 8%;
    height: 8%;
}

@media screen and (max-width: 414px) {
    .emas__published p {
        font-size: 13px;
        line-height: 1rem;
    }
    .emas__published h1 {
        font-size: 18px;
        line-height: 2rem;
    }
    .emas__published img {
        width: 15%;
        height: 30%;
    }
}