.emas__home-market {
    /* background-color: #8da8a7; */
    background-color: whitesmoke;
    padding: 10vw 10vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    z-index: -10;
}

.emas__home-market_container {
    color: black;
    width: 50%;
}

.emas__home-market_container h1 {
    font-size: 60px;
}

.emas__home-market_container p {
    font-size: 20px;
    line-height: 2rem;
}

.emas__home-market_container button {
    margin-top: 2rem;
    float: left;
    cursor: pointer;
    background-color: var(--container-hover-color);
    border: none;
    width: 100%;
    color: whitesmoke;
    padding: 1.5rem 2vw;
    font-size: 25px;
    font-weight: bold;
}

.emas__home-market_container button:hover {
    background-color: var(--container-hover-color-2);
}

.emas__home-market_solutions {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.emas__home-market_solutions p{
    font-size: 22px;
}

.emas__home-market_solutions div {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

.emas__home-market_solutions svg {
    position: relative;
    top: 1.6rem;
}

.svg2 {
    display: none;
}

@media screen and (max-width: 1400px) {
    .emas__home-market {
        padding: 10vw 7vw;
    }

    .emas__home-market_container h1 {
        font-size: 45px;
    }


    .emas__home-market_solutions p{
        font-size: 20px;
    }
}


@media screen and (max-width: 1400px) {
    .emas__home-market {
        padding: 10vw 12vw;
        flex-direction: column;
    }

    .emas__home-market_container {
        width: 100%;
    }

    .emas__home-market_solutions {
        width: 100%;
        padding-top: 4rem;
        align-items: center;
    }
    .svg1 {
        display: none;
    }
    .svg2 {
        display: flex;
    }
    .emas__home-market_solutions svg {
        position: relative;
        top: 1.3rem;
    }
    .emas__home-market_container button {
        padding: 1.3rem 2vw;
    }

}

@media screen and (max-width: 414px) {
    .emas__home-market {
        padding: 10vw 2rem;
    }

    .emas__home-market_container h1 {
        font-size: 40px;
    }
    .emas__home-market_solutions {
        align-items: flex-start;
        padding-top: 2rem;
    }
    .emas__home-market_solutions div {
        
    }
    .emas__home-market_solutions svg {
        position: relative;
    }
    .emas__home-market_container button {
        padding: 1.3rem 2vw;
    }

}