.emas__eleccontent {
    padding: 10vw 10vw;
    display: flex;
    flex-direction: row;
    color: white;
    align-items: center;
}

.emas__eleccontent-content {
    font-size: 1.7vw;
    padding: 5vw;
    width: 50%;
}

.emas__eleccontent img {
    width: 35vw;
}

.emas__eleccontent-image {
    width: 50%;
}

@media screen and (max-width: 414px) {
    .emas__eleccontent {
        padding: 3rem 2rem;
        padding-bottom: 6rem;
        display: flex;
        flex-direction: column;
        color: white;
        align-items: center;
    }

    .emas__eleccontent-content {
        font-size: 15px;
        width: 100%;
    }
    
    .emas__eleccontent-image{
        width: 100%;
    }

    .emas__eleccontent-image img {
        width: 100%;
    }
}