.emas__article {
    display: flex;
    flex-direction: column;
    width: 32rem;
    padding: 1rem 3rem;
    border-right: 2px solid gray;
    cursor: pointer;
}

.emas__article:hover {
    background-color: aliceblue;
}

.emas__article img {
    margin-bottom: 1rem;
}

.emas__article-heading {
    height: 8rem;
}

.emas__article h1 {
    font-size: 30px;
}

.emas__article h2 {
    font-size: 18px;
    text-align: right;
}

@media screen and (max-width: 414px){
    .emas__article {
        width: 16rem;
        padding: 1rem 2rem;
        border-right: 2px solid gray;
        cursor: pointer;
        padding-bottom: 0;
    }
    
    .emas__article:hover {
        background-color: aliceblue;
    }
    
    .emas__article img {
        margin-bottom: 0rem;
    }
    
    .emas__article-heading {
        height: 7rem;
        overflow-y: hidden;
    }
    
    .emas__article h1 {
        font-size: 20px;
    }
    
    .emas__article h2 {
        font-size: 15px;
        margin: 0 0;
        text-align: right;
    }
    .emas__article p {
        font-size: 14px;
    }
    .emas__article-description {
        overflow-y: hidden;
        height: 7rem;
    }
}