.emas__dashnavbar {
    padding: 0rem 7rem;
    padding-top: 7rem;
    background-color: whitesmoke;
    border-bottom: 1px solid rgba(128, 128, 128, 0.397);
}

.emas__dashnavbar-top {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.emas__dashnavbar-top_signout button {
    cursor: pointer;
    background-color: var(--container-color);
    color: white;
    padding: 0.8rem 2.5rem;
    font-size: 20px;
    font-weight: bold;
    border: none;
    border-radius: 10px;
}

.emas__dashnavbar-top_signout button:hover {
    background-color: var(--container-hover-color);
}

.emas__dashnavbar-links {
    display: flex;
    flex-direction: row;
    padding: 0.5rem 0rem;
}

.emas__dashnavbar-links button {
    cursor: pointer;
    font-size: 18px;
    border: none;
    background-color: whitesmoke;
    padding: 0.5rem 0rem;
    padding-right: 10rem;
    color: black;
}