.emas__elecheader {
    padding: 12rem 0;
    background-image: url("../../../assets/images/homepage/Fremantlesky.jpeg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
}

.emas__elecheader-content {
    color: whitesmoke;
    display: flex;
    flex-direction: column;
    justify-content: center;    
    max-width: 50vw;
}

.emas__elecheader-content h1 {
    font-size: 45px;
}

.emas__elecheader-content p {
    line-height: 40px;
    font-size: 25px;
    font-weight: 600;
}

.emas__elecheader button {
    float: right;
    cursor: pointer;
    background-color: var(--container-hover-color);
    border: none;
    color: whitesmoke;
    padding: 1.5rem 2rem;
    font-size: 25px;
    font-weight: bold;
}

.emas__elecheader button:hover {
    background-color: var(--container-hover-color-2);
}

@media screen and (max-width: 414px) {

    .emas__elecheader {
        padding: 7rem 0;
    }
    .emas__elecheader-content h1 {
        font-size: 25px;
    }

    .emas__elecheader-content {
        max-width: 80vw;
    }

    .emas__elecheader-content p {
        line-height: normal;
        font-size: 18px;
        font-weight: normal;
    }
    
    .emas__elecheader button {
        margin-top: 1rem;
        padding: 1.1rem 2rem;
        font-size: 20px;
        font-weight: bold;
    }
    
}