.emas__clientlogin {
    display: flex;
    justify-content: center;
}

.emas__clientlogin-container {
    background-color: white;
    display: flex;
    flex-direction: column;
    margin: 12rem 0rem;
    padding: 3vw 5vw;
    width: 20rem;
    border-radius: 1rem;
    padding-bottom: 8rem;
}

.emas__clientlogin h1 {
    font-size: 35px;
    text-align: center;
}

.emas__clientlogin p {
    font-size: 20px;
}

.emas__clientlogin input {
    border-color: gray;
    border-radius: 5px;
    padding: 1rem 1rem;
    font-size: 18px;
    width: 90%;
}

.emas__clientlogin button {
    font-weight: bold;
    width: 100%;
    cursor: pointer;
    margin-top: 3rem;
    padding: 1.1rem 1rem;
    background-color: var(--container-hover-color);
    font-size: 20px;
    color: white;
    border: none;
    border-radius: 5px;
}

.emas__clientlogin button:hover {
    background-color: var(--container-hover-color-2);
}

@media screen and (max-width: 414px) {
    .emas__clientlogin-container {
        margin-top: 12rem;
        width: 16rem;
        padding: 2rem 3rem;
        padding-bottom: 4rem;
    }
    .emas__clientlogin input {
        width: 85%;
        padding: 0.5rem 1rem;
        font-size: 16px;
    }
}
