.emas__aboutus {
    display: flex;
    justify-content: center;
    background-image: url("../../../assets/images/homepage/View1.jpeg");
    background-repeat: no-repeat;
    background-size: cover;
}

.emas__aboutus-header {
    color: white;
    padding: 16vw 30vw;
}

.emas__aboutus-non {
    background-color: rgba(0, 0, 0, 0.658);
}

.emas__aboutus-header_scroll {
    background-color: none;
    max-width: 80vw;
}

.emas__aboutus-header h1 {
    font-size: 3vw;
}

.emas__aboutus-header p {
    font-size: 2vw;
    font-weight: 600;
}

.emas__aboutus-header_svg {
    display: flex;
    align-items: center;
    justify-content: center;
}

.emas__aboutus-header svg {
    position: relative;
    bottom: 1rem;
    align-self: center;
    cursor: pointer;
}

@media screen and (max-width: 414px) {
    .emas__aboutus-header {
        color: white;
        padding: 10rem 2rem;
    }

    .emas__aboutus-header_scroll {
        background-color: none;
        max-width: 100%;
    }
    
    .emas__aboutus-header h1 {
        font-size: 35px;
    }
    
    .emas__aboutus-header p {
        font-size: 20px;
        font-weight: normal;
    }
    
    .emas__aboutus-header_svg {
        display: none;
    }
    
}

