.emas__models {
    display: flex;
    flex-direction: column;
    background-color: var(--container-hover-color-2);
    color: white;
    background-image: url("../../../assets/images/homepage/Fremantlesky.jpeg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 10vw 12vw;
}

.emas__models-container {
    display: flex;
    flex-direction: row;
}

.emas__models h1 {
    font-size: 70px;
}

.emas__models-container_text {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.emas__models-container p {
    font-weight: 500;
    font-size: 22px;
    line-height: 2rem;
}

.emas__models-container_box {
    margin-left: 5rem;
    width: 50%;
    display: flex;
    flex-direction: column;
    color:black;
    background-color: whitesmoke;
    padding: 2rem 4rem;
    border-bottom: 10px solid rgb(255, 94, 0);
}

.emas__models-container_box h2 {
    text-align: center;
    font-size: 2.2vw;
    margin-bottom: 1rem;
}

.emas__models-container_box p {
    text-align: left;
    font-size: 1vw;
    line-height: 1.9rem;
}

.emas__models-container button {
    cursor: pointer;
    background-color: var(--container-hover-color);
    border: none;
    width: 100%;
    color: whitesmoke;
    padding: 1.5rem 2vw;
    font-size: 25px;
    font-weight: bold;
}

.emas__models-container button:hover {
    background-color: var(--container-hover-color-2);
}

@media screen and (max-width: 1000px) {
    .emas__models h1 {
        font-size: 50px;
    }
    .emas__models-container {
        flex-direction: column;
    }
    .emas__models-container_text {
        width: 100%;
    }
    .emas__models-container_box {
        width: 100%;
        margin-left: 0;
        margin-top: 3rem;
        padding: 2rem 0rem;
    }

    .emas__models-container_box h2 {
        padding: 0rem 3rem;
        text-align: center;
        font-size: 35px;
        margin-bottom: 1rem;
    }
    
    .emas__models-container_box p {
        padding: 0rem 3rem;
        text-align: left;
        font-size: 18px;
        line-height: normal;
    }
}

@media screen and (max-width: 414px) {
    .emas__models {
        padding: 3rem 2rem;
    }
    .emas__models h1 {
        font-size: 35px;
        margin-bottom: 0rem;
    }
    .emas__models-container p {
        line-height: normal;
    }
    .emas__models-container_box h2 {
        padding: 0rem 2rem;
        text-align: left;
        font-size: 35px;
        margin-bottom: 1rem;
    }
    
    .emas__models-container_box p {
        padding: 0rem 2rem;
        text-align: left;
        font-size: 18px;
        line-height: normal;
    }
}