.emas__newsbar {
    padding: 6rem 0;
    padding-bottom: 12rem;
    background-color: whitesmoke;
    display: flex;
    flex-direction: column;
}

.emas__newsbar-heading {
    text-align: center;
    padding: 2rem 0rem;
    font-size: 25px;
}

.emas__newsbar-articles {
    display: flex;
    flex-direction: row;
    overflow-x: scroll;
}

@media screen and (max-width: 414px) {
    .emas__newsbar {
        padding: 2rem 0;
        padding-bottom: 4rem;
    }
    .emas__newsbar-heading {
        text-align: center;
        padding: 2rem 2rem;
    }
    .emas__newsbar-heading h1 {
        font-size: 32px;
    }
    
}