.emas__home-info {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 10vw 11vw;
    background-color: var(--container-hover-color-2);
    justify-content: space-between;
}

.emas__home-info_container {
    color: white;
    /* width: 40vw; */
    width: 60%;
}

.emas__home-info_container h1 {
    text-align: left;
    font-size: 50px;
    padding-bottom: 2rem;
    /* max-width: 40vw; */
}

.emas__home-info_container p {
    font-size: 22px;
}

.emas__home-info_image {
    width: 40%;
}

.emas__home-info_image img {
    width: 100%;
    padding-left: 5vw;
    /* border-radius: 2rem; */
}


@media screen and (max-width: 1400px) {
    .emas__home-info_image {
        display: none
    }
    .emas__home-info_container {
        width: 100%;
    }
}

@media screen and (max-width: 414px) {
    .emas__home-info {
        padding: 3rem 2rem;
        background-color: var(--container-hover-color-2);
        justify-content: space-between;
    }
    
    .emas__home-info_container h1 {
        font-size: 35px;
        padding-bottom: 0rem;
    }
    
    .emas__home-info_container p {
        font-size: 20px;
    }
    

}