.emas__gas {
    background-image: url("../../../assets/images/homepage/Fremantlesky.jpeg");
    display: flex;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-color: var(--container-hover-color);
    padding: 15rem 20vw;
}

.emas__gas-header {
    display: flex;
    flex-direction: column;
    align-items: left;
    color: whitesmoke;
    max-width: 50vw;
}

.emas__gas-header h1 {
    font-size: 4vw;
}

.emas__gas-header p {
    font-size: 1.5vw;
    font-weight: 500;
}

.emas__gas button {
    float: right;
    width: 22vw;
    cursor: pointer;
    background-color: var(--container-hover-color);
    border: none;
    color: whitesmoke;
    padding: 1.5vw 2vw;
    font-size: 1.5vw;
    font-weight: bold;
}

.emas__gas button:hover {
    background-color: var(--container-hover-color-2);
}

@media screen and (max-width: 414px) {
    .emas__gas {
        padding: 10rem 2rem;
    }
    
    .emas__gas-header {
        max-width: 100%;
        width: 100%;
    }
    
    .emas__gas-header h1 {
        font-size: 26px;
    }
    
    .emas__gas-header p {
        font-size: 17px;
        font-weight: 500;
    }
    
    .emas__gas button {
        margin-top: 1rem;
        width: 16rem;
        padding: 1rem 0rem;
        font-size: 19px;
    }
    
}