.emas__contactus {
    background-image: url("../../../assets/images/homepage/View1.jpeg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    padding: 8vw 0rem;
}

.emas__contactus-content {
    max-width: 35vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: whitesmoke;
    padding: 4vw 7vw;
    border-bottom: 15px solid var(--container-hover-color-2);
}

.emas__contactus-content h1 {
    font-size: 2.2vw;
}

.emas__contactus-content h2 {
    font-size: 1.7vw;
}

.emas__contactus-content p {
    display: flex;
    font-size: 1.4vw;
    align-items: center;
    line-height: 0px;
}

@media screen and (max-width: 414px) {
    .emas__contactus {
        padding: 10rem 0rem;
    }
    
    .emas__contactus-content {
        max-width: 14rem;
        padding: 2rem 2rem;
        border-bottom: 5px solid var(--container-hover-color-2);
    }
    
    .emas__contactus-content h1 {
        font-size: 18px;
    }
    
    .emas__contactus-content h2 {
        font-size: 14px;
    }
    
    .emas__contactus-content p {
        display: flex;
        font-size: 10px;
        align-items: center;
        line-height: 0px;
    }
}