.emas__team {
    background-color: whitesmoke;
    padding: 7vw 0 ;
    padding-bottom: 0rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.emas__team-head {
    text-align: center;
    max-width: 55vw;
    padding-bottom: 2vw;
}

.emas__team-head h1 {
    font-size: 3vw;
}

.emas__team-head p {
    font-size: 1.8vw;
}

.emas__team-container {
    display: flex;
    padding: 5vw 0rem;
    grid-template-columns: auto auto;
    justify-content: center;
    gap: 5vw;
}

.emas__team-container_greg, .emas__team-container_peter {
    background-color: var(--container-hover-color);
    border-radius: 3vw;
    text-align: center;
    color: white;
    display: flex;
    flex-direction: row;
    max-width: 35vw;
    align-items: center;
    padding: 6vw 2vw;
    box-shadow: 4px 5px 7.5px rgba(0, 0 ,0, 0.3);
}

.emas__team-container_greg div, .emas__team-container_peter div {
    display: flex;
    text-align: left;
    flex-direction: column;
    align-items: flex-start;
}

.emas__team-container img {
    align-self: center;
    border-radius: 10vw;
    height: 8vw;
    margin: 0rem 2vw;
}

.emas__team-container h1 {
    font-size: 2vw;
}
.emas__team-container p {
    margin-top: 0;
    font-size: 1.2vw;
}

.emas__team-container h2 {
    margin-top: 0;
    font-size: 1.3vw;
}


@media screen and (max-width: 1400px) {
    .emas__team-container_greg, .emas__team-container_peter {
        padding: 4vw 4vw;
        flex-direction: column;
        max-width: 28vw;
    }
    .emas__team-container img {
        height: 14vw;
        padding: 2vw 2vw;
    }
}

@media screen and (max-width: 414px) {
    .emas__team {
        padding: 2rem 0 ;
    }
    
    .emas__team-head {
        text-align: center;
        max-width: 100%;
        padding: 0rem 3rem;
        padding-bottom: 1rem;
    }
    
    .emas__team-head h1 {
        font-size: 24px;
    }
    
    .emas__team-head p {
        font-size: 12px;
    }
    .emas__team-container {
        flex-direction: column;
    }
    .emas__team-container_greg, .emas__team-container_peter {
        flex-direction: column;
        max-width: 13rem;
        padding: 3rem 2rem;
    }
    .emas__team-container h1 {
        font-size: 16px;
    }
    .emas__team-container p {
        font-size: 9px;
    }
    
    .emas__team-container h2 {
        margin-top: 0;
        font-size: 11px;
    }
    .emas__team-container img {
        border-radius: 4rem;
        height: 6rem;
        margin: 0rem 2vw;
        padding-bottom: 1rem;
    }
}