.emas__articles {

}
.emas__articles-container {
    margin: 10rem 25rem;
    border-radius: 15px;
    padding: 5rem 8rem;
    padding-top: 8rem;
    box-shadow: 0px 0px 7.5px rgba(0, 0 ,0, 0.1);
    background-color: white;
}


.emas__articles-container h1 {
    font-size: 35px;
}

.emas__articles-container p {
    line-height: 2rem;
    font-size: large;
}

.emas__articles-container a {
    word-wrap: break-word;
    font-weight: 600;
    text-decoration: none;
    color: rgb(15, 114, 226);
}

.emas__articles-container img {
    width: 100%
}

.emas__articles-container_headImage img {
    height: 100%;
    border-radius: 10px;
    width: 80%;
}

.emas__articles-container_headImage {
    border-radius: 10px;
    margin: 1rem 0rem;
    margin-bottom: 0.5rem;
}

.emas__articles-container_body {
    padding: 1rem 0rem;
}

.emas__articles-container_photo {
    font-size: 14px;
    text-align: center;
    padding-bottom: 0.5rem;
}

.emas__articles-container_publish {
    display: flex;
    justify-content: flex-end;
    margin-bottom: -6rem;
}
@media screen and (max-width: 1800px) {
    .emas__articles-container {
        margin: 7rem 6rem;

        padding: 1rem 8rem;
    } 
}
@media screen and (max-width: 414px) {
    .emas__articles-container {
        margin: 7rem 1rem;
        border-radius: 15px;
        padding: 1rem 2rem;
    }
    .emas__articles-container p {
        line-height: 1.8rem;
        font-size: large;
    }
    .emas__articles-container h1 {
        font-size: 21px;
        margin-top: 1.5rem;
        margin-bottom: -0.5rem;
    }
    .emas__articles-container_publish p {
        margin-bottom: 0rem;
        font-size: 16px;
        line-height: normal;
    }
    .emas__articles-container i {
        font-size: 11px;
    }
}