.emas__footer {
 display: grid;
 grid-template-columns: auto auto auto auto;
 justify-content: space-around;
 padding: 8vw 8vw;
}

.emas__footer div {
    max-width: 15vw;
    display: flex;
    flex-direction: column;
}

.emas__footer h1 {
    /* color: rgb(0, 14, 20); */
    color: whitesmoke;
    font-size: 1.3vw;
}

.emas__footer a {
    color: whitesmoke;
    padding: 1.5vw 0rem;
    font-size: 1.5vw;
    line-height: 3vw;
    font-weight: 600;
}

@media screen and (max-width: 414px) {
    .emas__footer {
        grid-template-columns: auto auto;
        row-gap: 1rem;
        padding: 2rem 1rem;
        column-gap: 0rem;
    }

    .emas__footer h1 {
        font-size: 10px;
    }
    .emas__footer a {
        font-size: 10px;
    }

    .emas__footer div {
        max-width: 7rem;
    }
}