.emas__navbar {
    z-index: 999;
    margin: 0 0;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0rem 3vw;
}

.emas__navbar-scroll {
    z-index: 999;
    background: var(--background-color);
    box-shadow: 4px 3px 7.5px rgba(0, 0 ,0, 0.3);
}

.emas__navbar-heading {
    padding-left: 2rem;
    display: flex;
    align-items: center;
}

.emas__navbar-heading h1 {
    padding: 0rem;
}

.emas__navbar-links {
    display: flex;
    margin-left: auto;
}

.emas__navbar-links a {
    font-size: 23px;
    font-weight: bold;
}

.emas__navbar-links_menu {
    display: flex;
}

.emas__navbar-links_menu svg {
    color: white;
}

.emas__navbar-links_menu-off, .emas__navbar-links_menu-on {
    padding: 1.72rem 0rem;
    width: 11rem;
    text-align: center;
    margin: 0rem 0.5rem;
    display: flex;
    flex-direction: column;
}

.emas__navbar-links_menu-off:after {
    content: '';
    display: block;
    margin: auto;
    height: 3px;
    width: 0px;
    border-bottom: 3px solid white;
    transition: border-bottom .5s ease-in-out;
    transition: width .5s ease-in-out;
}

.emas__navbar-links_menu-off:hover:after {
    width: 100%;
}

.emas__navbar-links_menu-on {
    display: block;
    height: 0.6rem;
    border-bottom: 3px solid white;
}

.emas__navbar-links_menu-content {
    display: flex;
    align-items: center;
    justify-content: center;
}

.emas__navbar-links_menu-open {
    z-index: 0;
    display: none;
    position: fixed;
    flex-direction: column;
    margin-top: 3.8rem;
    padding: 0rem 0;
    width: 11rem;
    background-color: rgba(255, 255, 255, 0.11);
}

.emas__navbar-links_menu-open_scroll {
    background-color: var(--container-color);
    box-shadow: 0px 3px 7.5px rgba(0, 0 ,0, 0.3);
}

.emas__navbar-links_menu-open a {
    font-size: 14px;
    padding: 2rem 0rem;
    align-items: center;
}

.emas__navbar-links_menu-open a:hover {
    background-color: rgba(255, 255, 255, 0.103);
}
.emas__navbar-links_menu-open_scroll a:hover {
    background-color: var(--container-hover-color)
}

.emas__navbar-links_menu div:hover .emas__navbar-links_menu-open {
    display: flex;
}

.emas__navbar-links_menu-open:hover .emas__navbar-links_menu-open {
    display: flex;
}

.emas__navbar-sidemenu {
    display: none;
    position: fixed;
    right: 3rem;
}

.emas__navbar-sidemenu_container {
    background-color: var(--container-color);
    position: fixed;
    top: 4.5rem;
    left: 0.25rem;
    right: 0.25rem;
    display: block;
    flex-direction: column;
    text-align: center;
    font-size: 17px;
    font-weight: 500;
    background-color: none;
    box-shadow: 0px 3px 7.5px rgba(0, 0 ,0, 0.3);
}

.emas__navbar-sidemenu_container-link {
    display: flex;
    flex-direction: column;
    padding: 1rem 0rem;
}

/* .emas__navbar-sidemenu_container-link a {
    padding-bottom: 1rem;
} */

.emas__navbar-sidemenu_container-link_row {
    display: flex;
    flex-direction: row;
    text-align: center;
    justify-content: center;
}

.emas__navbar-sidemenu_container-link:hover {
    background-color: rgba(250, 250, 250, 0.233);
    background-color: var(--container-hover-color);
}


.emas__navbar-sidemenu_container-link svg {
    display: flex;
    position: relative;
    top: 0.2rem;
    left: 0.3rem;
    color: white;
}

.emas__navbar-sidemenu_container-link_open {
    padding-top: 1rem;
    display: flex;
    flex-direction: column;
    font-size: 15px;
    margin-bottom: -1rem;
}

.emas__navbar-sidemenu_container-link_open a {
    padding: 1rem 0rem;
}

.emas__navbar-sidemenu_container-link_open a:hover {
    background-color: var(--container-hover-color-2);
}


@media screen and (max-width: 1250px) {
    .emas__navbar h1 {
        font-size: 25px;
    }
    .emas__navbar:hover {
        background: var(--background-color);
    }
    .emas__navbar-links {
        display: none;
    }

    .emas__navbar-sidemenu {
        display: flex;
    }

}

@media screen and (max-width: 414px) {
    .emas__navbar-sidemenu {
        right: 2rem;
    }
}