.emas__home-forecast {
    padding: 14vw 10vw;
}

.emas__home-forecast_grid {
    row-gap: 8vw;
    column-gap: 10vw;
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-around;
}

.emas__home-forecast_grid div {
    padding: 3rem 3rem;
    width: 32vw;
    max-height: 12rem;
}
.emas__home-forecast_grid p {
    margin-bottom: 1rem;
}

.emas__home-forecast_grid-coal, .emas__home-forecast_grid-renewable, .emas__home-forecast_grid-other {
    background-color: whitesmoke;
}


.emas__home-forecast_grid-coal {
    border-bottom: 10px solid rgb(252, 0, 252);
}

.emas__home-forecast_grid-renewable {
    border-bottom: 10px solid red; 
}

.emas__home-forecast_grid-other {
    border-bottom: 10px solid rgb(0, 255, 13); 
}

.emas__home-forecast_grid-head {
    color: white;
    font-size: 40px;
    margin-top: -6rem;
}

.emas__home-forecast_top{
    display: none;
}

@media screen and (max-width: 1350px) {
    .emas__home-forecast_grid div {
        padding: 3rem 3rem;
        width: 32vw;
        max-height: 20rem;
    }
}

@media screen and (max-width: 414px) {
    .emas__home-forecast {
        padding: 2rem 0rem;
        padding-bottom: 5rem;
    }

    .emas__home-forecast_top{
        display: flex;
    }
    
    .emas__home-forecast h2 {
        padding: 1rem 2rem;
        font-size: 32px;
        color: white;
    }

    .emas__home-forecast_grid {
        row-gap: 4rem;
        padding: 0rem 5rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center; 
    }

    .emas__home-forecast_grid-head {
        display: none;
    }

    .emas__home-forecast_grid div {
        width: 100%;
        padding: 2rem 3rem;
        max-height: 26rem;
    }
}