
.emas__byline {
    padding: 1rem 1rem;
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid black;
    align-items: center;
    background-color: white;
    justify-content: flex-start;
    
}

.emas__byline-top {
    display: flex;
    flex-direction: column;
    padding-left: 1rem;
    
}

.emas__byline-top h1 {
    margin-bottom: -1rem;
    margin-top: 1.5rem;
    font-size: 20px;
}

.emas__byline-top p {
    font-size: 15px;
}

.emas__byline img {
    border-radius: 5rem;
}

@media screen and (max-width: 414px) {
    .emas__byline p {
        font-size: 13px;
        line-height: 1rem;
    }
    .emas__byline h1 {
        font-size: 18px;
        line-height: 2rem;
    }
    .emas__byline img {
        width: 15%;
        height: 30%;
    }
}